.container {
  display:flex;
  padding-left: 0;
  left: 0;
  margin-right: 0px;
  padding-top: 0px;
  overflow:hidden;
}
@media (min-width: 70rem){
  .container {
    max-width: 30%;
    min-width:15%;
  }
}
@media (max-width: 100rem){
  .container {
    width:0%
}
  .articlelist{
    padding-right: 32;
  }
}
.articlelist,
.articlelist > ul {
  padding-inline-start:0;
  /* width:16rem; */
  padding-top:27;
  list-style: none;
  text-decoration: none;
}
.articlelistitems,
.articlelistitems > li {
  list-style: none;
  text-decoration: none;
  font-size: .9rem;
  display: block;
}
.articlelistheading{
  margin-top: 1.7rem;
  margin-bottom: 0rem;
}
