.container {
  margin: 0;
  padding: 2.625rem .75rem;
  max-width: 100%;
}
@media (max-width: 80rem){
  .container {
    margin-right:.5rem;
  }
}
.contentarea {
  display:flex;
  max-width:100%;
}
.title {
  font-family: Montserrat, sans-serif;
  font-size: 2.5rem;
  font-weight: 900;
  line-height: 1.1;
  margin-top: 0;
  margin-bottom: 0;
  text-rendering: optimizeLegibility;
}
.title > a {
  color: var(--primary-color-light);
  text-decoration: none;
}
