@import url('https://fonts.googleapis.com/css?family=Merriweather:400,400i,700,700i,900|Montserrat:900');

:root {
  --primary-color: #0b4480;
  --primary-color-light: rgb(4, 104, 144);
  max-width: 100%;
}

:global * {
  box-sizing: border-box;
}

:global html {
  color: rgba(0, 0, 0, 0.9);
  font: 100%/1.75 Consolas, Georgia, serif;
}

:global body {
  font-kerning: normal;
  font-family: 'Consolas', monospace;
  font-feature-settings: "kern","clig";
  font-weight: 450;
  letter-spacing: 0em;
  margin: 0;
  padding: 0;
  word-wrap: break-word;
  max-width: 100%;
}

:global(pre),
:global(code) {
  font-family: Consolas, Menlo, Monaco, source-code-pro, "Courier New", monospace;
  font-size: .82rem;
  line-height: 1;
}
:global pre {
  border-radius: 1px;
  overflow: auto;
  padding: 1em;
}

:global a {
  color: var(--primary-color);
}
:global a:hover {
  text-decoration: none;
}

:global p,
:global pre,
:global ul,
:global ol {
  margin-top: 0;
  margin-bottom: 1.75rem;
  max-width: 100%;
}
